blockquote {
  font-size: 24px;
  font-style: italic;
  background: #cecece3d;
  color: rgb(122, 122, 122);
  border-left: 8px solid rgb(170, 170, 170);
  padding: 4px 16px;
  border-radius: 3px;
  margin: 0;
}

